html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.fijar-body {
    overflow: hidden; }

p {
  font-size: 2rem;
  color: #000; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0;
  text-align: center; }

h1 {
  font-size: 6rem;
  text-align: center; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.bg-verde {
  background-color: #4CB8B3; }

.bg-amarillo {
  background-color: #FEF46D; }

.header {
  background: #052024;
  background: linear-gradient(144deg, rgba(17, 18, 19, 0.171) 1%, rgba(23, 26, 39, 0.998) 17%, #f8f4fb 38%, rgba(20, 24, 17, 0.544) 74%, rgba(174, 137, 223, 0.196516) 87%, rgba(177, 225, 235, 0.269345) 100%); }

.titulo {
  text-align: center;
  padding: 2rem 0;
  color: white; }

.footer {
  text-align: center;
  padding: 2rem 0;
  background: #3f5efb;
  background: radial-gradient(circle, #080808 0%, #012c08 92%, #424444 100%);
  font-weight: 700; }

.imagen {
  text-align: center; }

.btn {
  padding: 2rem;
  background-color: blue;
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  width: 26rem;
  border-radius: 20px; }

.botones {
  margin: 10rem auto;
  display: flex;
  justify-content: space-evenly;
  transition: 0.8s; }
  .botones:hover {
    cursor: pointer;
    background-color: rgba(44, 42, 165, 0.473); }
  .botones-stop {
    margin: 10rem auto;
    transition: 0.8s;
    display: flex;
    justify-content: space-evenly;
    background-color: brown; }
    .botones-stop:hover {
      cursor: pointer;
      background-color: rgba(165, 42, 42, 0.473); }
  .botones-alterna {
    margin: 2rem auto;
    transition: 0.8s;
    display: flex;
    justify-content: space-evenly;
    background-color: #2aa52a; }
    .botones-alterna:hover {
      cursor: pointer;
      background-color: rgba(42, 165, 58, 0.473); }

.contadores {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: rgba(184, 184, 173, 0.137);
  text-align: center;
  align-items: center; }
  .contadores p {
    background-color: #32c8cd;
    width: 30rem;
    border-radius: 10px;
    padding: 2rem;
    font-weight: 700; }

.graficas {
  display: flex; }
  .graficas .cuadro {
    margin: 5rem;
    width: 10rem;
    height: 5rem;
    background-color: blue; }

.body {
  background-color: rgba(0, 81, 255, 0.521); }

.center {
  text-align: center;
  background-color: rgba(0, 139, 139, 0.281); }
